@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
body {
  font-family: "Montserrat", sans-serif;
}
.container {
  text-align: center;
  padding: 0.8em 1.2em;
}
header {
  display: flex;
  justify-content: space-between;
}
.hide-mobile {
  display: none;
}
.menu {
  width: 25px;
  margin-top: 45%;
}
.logo {
  width: 130px;
}
.server {
  width: 70%;
  margin: 2em auto 2em 25%;
}
h1,
.sub-head {
  position: relative;
  z-index: 3;
}
.sub-head {
  font-size: 1.1em;
}
.blue-container {
  width: 100%;
  background-color: #00c2ff;
  color: #fff;
  margin-top: 15%;
  padding: 4em 0 2em;
  border-top-left-radius: 40px;
  transform: skewY(-2deg);
}
.blue-container img {
  width: 14%;
}
.blue-container ul {
  transform: skewY(2deg);
}
.blue-container ul li {
  margin-bottom: 3em;
}
.blue-container ul li img.last-img {
  width: 8%;
}
.blue-container ul li p {
  padding: 0 0.5em;
}
.blue-container::before {
  content: "";
  width: 40px;
  height: 40px;
  background-color: #00c2ff;
  position: absolute;
  top: -39px;
  right: 0;
  z-index: -2;
}
.blue-container::after {
  content: "";
  width: 80px;
  height: 80px;
  background-color: #fff;
  position: absolute;
  top: -80px;
  right: 0;
  border-radius: 50%;
  z-index: -1;
}
.grey-container {
  background-color: #f6f6f6;
  padding: 4em 0 2em;
  margin-top: -3%;
}
.grey-container figcaption {
  font-weight: bold;
  color: #00c2ff;
}
.grey-container ul li:first-child {
  margin-bottom: 2em;
}
h2 {
  margin-top: 7%;
}
.cta {
  display: block;
  background-color: #00cc80;
  color: #fff;
  border-radius: 20px;
  padding: 0.5em 3em;
  width: calc(100% - 1em);
  margin: 5% auto 7%;
  border: none;
  font-size: 1.5em;
  position: relative;
  z-index: 4;
}
.footer-container {
  width: 100%;
  background-color: #3d444a;
  color: #fff;
  padding: 3em 0;
  border-top-right-radius: 40px;
  transform: skewY(2deg);
}
.footer-container img {
  transform: skewY(-2deg);
}
.footer-container .address-container {
  transform: skewY(-2deg);
}
.footer-container .container {
  transform: skewY(-2deg);
}
.footer-container ul.footer-links li {
  display: inline-block;
}
.footer-container ul.footer-links li a {
  color: #9d9d9d;
  display: inline-block;
  text-decoration: none;
  font-size: 0.8em;
  margin-top: 1em;
  margin-right: 1em;
}
.footer-container::before {
  content: "";
  width: 40px;
  height: 40px;
  background-color: #3d444a;
  position: absolute;
  top: -39px;
  left: 0;
  z-index: -2;
}
.footer-container::after {
  content: "";
  width: 80px;
  height: 80px;
  background-color: #fff;
  position: absolute;
  top: -80px;
  left: 0;
  border-radius: 50%;
  z-index: -1;
}
p,
blockquote {
  font-size: 0.9em;
  line-height: 1.6em;
}
ul {
  list-style-type: none;
}
nav ul {
  position: fixed;
  top: 0;
  right: 0;
  width: 60%;
  text-align: left;
  background-color: #24292c;
  height: 100%;
  z-index: 7;
  padding-top: 3em;
}
nav ul li a {
  text-decoration: none;
  display: block;
  width: 100%;
  padding: 1em 2em;
  background-color: #343b3f;
  color: #fff;
}
nav ul li a:hover {
  background-color: #41494e;
}
nav ul .exit-btn {
  margin-bottom: 1em;
  margin-top: -1.3em;
  text-align: right;
  padding: 0 1.4em;
}
nav ul .exit-btn img {
  width: 15px;
  cursor: pointer;
}
@media only screen and (min-width: 650px) {
  .server {
    width: 50%;
  }
  h1 {
    font-size: 3em;
    margin: 0;
  }
  nav ul {
    width: 40%;
  }
  .sub-head {
    font-size: 1.4em;
    margin-bottom: 12%;
  }
  .blue-container {
    margin: 0;
  }
  .blue-container img {
    width: 20%;
  }
  .blue-container ul {
    display: flex;
    margin-top: -1em;
    align-items: center;
  }
  .blue-container ul li {
    margin-bottom: 0;
  }
  .blue-container ul li img.last-img {
    width: 11%;
    margin-top: -6px;
  }
  .blue-container ul li p {
    padding: 0 0.8em;
  }
  .grey-container ul {
    display: flex;
    gap: 0 5%;
  }
  .grey-container ul blockquote {
    text-align: center;
    margin: 1em 0;
  }
}
@media only screen and (min-width: 875px) {
  .sub-head {
    margin-bottom: 15%;
  }
  .blue-container ul {
    margin: 0 0 4% 0;
  }
  .grey-container ul {
    margin: 4% 0 0 0;
  }
  .cta {
    width: 45%;
  }
}
@media only screen and (min-width: 1024px) {
  .cta {
    width: 50%;
  }
  .container {
    width: 80%;
    margin: 0 auto 13% auto;
  }
  .show-desktop {
    display: block;
    margin: 0 auto 13% auto;
  }
  .hide-desktop {
    display: none;
  }
  header nav ul {
    position: inherit;
    width: auto;
    background: none;
    height: auto;
    display: flex;
    text-align: right;
  }
  header nav ul li {
    float: left;
  }
  header nav ul li a {
    color: #000;
    background-color: inherit;
    padding: 1em 2em;
  }
  header nav ul li a:hover {
    color: #fff;
  }
  .scroll {
    width: 30px;
    animation: move 1s infinite alternate;
  }
  @keyframes move {
    0% {
      transform: translateY(0px);
    }
    100% {
      transform: translateY(20px);
    }
  }
}
@media only screen and (min-width: 1600px) {
  .server {
    width: 40%;
    margin: 5em auto 4em 15%;
  }
  .container {
    width: 70%;
  }
  .blue-container {
    padding: 7em 0 2em;
    .container {
      .last-li {
        display: flex;
        flex-direction: column;
        align-items: center;
      }
      p {
        width: 20rem;
        height: 200px;
        font-size: 1.5rem;
      }
    }
  }
  .blue-container .last-img {
    width: 8%;
    margin-top: -4px;
  }
  .blue-container img {
    width: 15%;
  }
  .blue-container p,
  .blue-container blockquote {
    font-size: 15.3em;
  }
  .grey-container {
    padding: 6em 0 4em;
  }
  .grey-container h2 {
    margin-top: 4em;
  }
}
